const asyncRouter = [
	{
		// 主页路由
		key: 0,
		path: '/home',
		name: 'home',
		component: 'home',
		children: [
			{
				headerTitle: '农业总览',
				key: 256,
				path: 'overview',
				name: 'overview',
				component: 'overview/index',
				meta: {
					keepAlive: true // 缓存
				}
			},
			{
				headerTitle: '种植业',
				key: 4096,
				path: 'planting',
				name: 'planting',
				component: 'planting/index'
			},
			{
				headerTitle: '蔬菜管理',
				key: 8192,
				path: 'vegetables',
				name: 'vegetables',
				component: 'vegetables/index'
			},
			{
				headerTitle: '渔业管理',
				key: 16384,
				path: 'fisheries',
				name: 'fisheries',
				component: 'fisheries/index'
			},
			{
				headerTitle: '畜牧管理',
				key: 65536,
				path: 'animalHusbandryManage',
				name: 'animalHusbandryManage',
				component: 'animalHusbandryManage/index'
			},
			{
				headerTitle: '安全监管',
				key: 1024,
				path: 'safeSupervise',
				name: 'safeSupervise',
				component: 'safeSupervise/index'
			},
			{
				headerTitle: '农产品品牌',
				key: 2048,
				path: 'farmProductBrand',
				name: 'farmProductBrand',
				component: 'farmProductBrand/index',
				meta: {
					keepAlive: true // 缓存
				}
			},
			{
				headerTitle: '产销对接',
				key: 32768,
				path: 'productionSales',
				name: 'productionSales',
				component: 'productionSales/index'
			},
			{
				headerTitle: '土地资源监管',
				key: 128,
				path: 'gismap',
				name: 'gismap',
				component: 'gismap/index'
			},
			{
				headerTitle: '自然资源监管',
				key: 1,
				path: 'landSource',
				name: 'landSource',
				component: 'landSource'
			},
			{
				headerTitle: '经营主体监管',
				key: 2,
				path: 'operators',
				name: 'operators',
				component: 'operators'
			},
			{
				headerTitle: '农产品监管',
				key: 4,
				path: 'produce',
				name: 'produce',
				component: 'produce'
			},
			{
				headerTitle: '投入品监管',
				key: 8,
				path: 'inputs',
				name: 'inputs',
				component: 'inputs'
			},
			{
				headerTitle: '市场监管',
				key: 16,
				path: 'market',
				name: 'market',
				component: 'market'
			},
			{
				headerTitle: '物联网监控',
				key: 512,
				path: 'envMonitoring',
				name: 'envMonitoring',
				component: 'envMonitoring/index'
			},
			{
				headerTitle: '气象监测',
				key: 64,
				path: 'sceneFrame',
				name: 'sceneFrame',
				component: 'sceneFrame'
			},
			{
				headerTitle: '气象监测',
				key: 32,
				path: 'scene',
				name: 'scene',
				component: 'scene'
			},
			{
				// 土地监管--子页面:地块
				key: 0,
				path: 'plotlist',
				name: 'plotlist',
				component: 'plotlist'
			}
		]
	},
	{
		// 基地详情页 神农口袋
		key: 0,
		path: '/farmdetail/:farmid',
		name: 'farmdetail',
		component: 'farmdetail/farmdetail'
	},
	{
		// 基地详情页 种植业系统版本
		key: 0,
		path: '/gisfarmdetail/:companyCode',
		name: 'gisfarmdetail',
		component: 'farmdetail/farmdetail-other'
	},
	{
		// 环境监控 视频详情页
		key: 0,
		path: '/videoDetail/:baseid',
		name: 'videoDetail',
		component: 'envMonitoring/videoDetail'
	},
	{
		// 环境监控 物联网
		key: 0,
		path: '/iotDevice/:baseid',
		name: 'iotDevice',
		component: 'envMonitoring/iotDevice'
	},
	{
		// 绿色认证列表
		key: 0,
		path: '/greenCertList',
		name: 'greenCertList',
		component: 'overview/greenCertList'
	},
	{
		// 执法监管列表
		key: 0,
		path: '/lawSuperviseList',
		name: 'lawSuperviseList',
		component: 'overview/lawSuperviseList'
	},
	{
		// 经营主体列表
		key: 0,
		path: '/operateSubjectList',
		name: 'operateSubjectList',
		component: 'overview/operateSubjectList'
	},
	{
		// 在田主体列表
		key: 0,
		path: '/fieldsStatisticsList',
		name: 'fieldsStatisticsList',
		component: 'overview/fieldsStatisticsList'
	},
	{
		// 农产品品牌明细
		key: 0,
		path: '/farmProductBrandList',
		name: 'farmProductBrandList',
		component: 'farmProductBrand/farmProductBrandList'
	},
	{
		// 生产记录列表（农事操作）
		key: 0,
		path: '/farmRecordList',
		name: 'farmRecordList',
		component: 'overview/farmRecordList'
	},
	{
		// 生产记录列表（农事操作）
		key: 0,
		path: '/farmRecordListOther',
		name: 'farmRecordListOther',
		component: 'overview/farmRecordListOther'
	},
	{
		// 导出列表
		key: 0,
		path: '/downloadExcelList',
		name: 'downloadExcelList',
		component: 'downloadExcelList/index'
	},

	// 以下是开发工具页面 正式包不会打进去
	{
		// 测试服务器会有此页面
		key: process.env.NODE_ENV === 'development' ? 0 : 10000,
		path: '/map',
		name: 'map',
		component: 'recycle-bin/map'
	},
	{
		// 测试服务器会有此页面
		key: process.env.NODE_ENV === 'development' ? 0 : 10000,
		path: '/gmap',
		name: 'gmap',
		component: 'recycle-bin/gmap'
	},
	{
		// 测试服务器会有此页面
		key: process.env.NODE_ENV === 'development' ? 0 : 10000,
		path: '/demo',
		name: 'demo',
		component: 'demo/gis'
	}
];
export default asyncRouter;



